import { Injectable } from '@angular/core';
import { AlertController} from '@ionic/angular';
import { ToastController }      from '@ionic/angular';
import { Events }               from './events-service';

import 'rxjs/add/operator/map';

import { BluetoothMessage } from '../models/bluetooth-message';
import { BluetoothTransactionRequest } from '../models/bluetooth-message';
import { BluetoothAcknowledgement } from '../models/bluetooth-message';
import { BluetoothResponse } from '../models/bluetooth-message';
import { BluetoothUUID } from '../models/bluetooth-message';

import { Storage } from '@ionic/storage-angular';

declare var ble:any;

@Injectable({
  providedIn: 'root'
})
export class BluetoothService {

    protected responses:Array<BluetoothResponse> = [];
    protected deviceCount:number;
    protected connected: boolean;
    protected deviceCheckInterval:any;
    protected previouslyConnectedMeter: string;
    
    protected devices:Array<any>;
    
    protected chunkedResponse:string;
    
    
    constructor(private events: Events, private toastController:ToastController, private alertCtrl:AlertController, private storage:Storage) {

        this.storage.create();
        this.deviceCount = 0;
        this.connected = false;
        
        //this.deviceCheckInterval = setInterval(() => {
            //this.checkDevices();
        //},10000);        
        
        this.previouslyConnectedMeter = "";
        this.storage.get("previouslyConnectedMeter").then((uuid) => {
            if (uuid){
                this.previouslyConnectedMeter = uuid;
            }
        }).catch(() => {
            
        });
        
        
        this.devices = [];
        
        this.chunkedResponse = "";
    }
  

    
    private checkDevices(){
        this.scanDevices().then((devices:Array<any>) => {

            let count = devices.length;
            if (count !== this.deviceCount){
                this.deviceCount = count;
                //prompt user if they want to connect to differant device
                this.changeDevice(devices);
            }                
        });               
    }
    

    public connect(device:any) {
        //console.log('[BluetoothService] - connect() :: ');
 
            this.events.publish('meter:connecting');
            
            ble.isConnected(device.address, () => {
                this.onConnect(device, {status:"device was already connected"});
            }, () => {
                ble.connect(device.address,
                (deviceDetails) => this.onConnect(device, deviceDetails),
                (failure) => this.onFail(failure));    
            });          
            


    }

    public list() {
        //console.log(`[BluetoothService] - list() :: List bound devices`);
        this.responses = new Array();
        this.events.publish('meter:listing');
        
        
        this.scanDevices().then((devices:Array<any>) => {

            if (devices.length === 1){
                this.connect(devices[0]);
                this.storage.set("previouslyConnectedMeter", devices[0].address);
                return;
            }
            else{
                if (this.previouslyConnectedMeter){
                    for (let device of devices){
                        if (device.address === this.previouslyConnectedMeter){
                            this.connect(device);
                            return;
                        }
                    }
                }
                else{
                    this.selectDevice(devices);
                }
            }                
        });

        
        
    }
    
    private scanDevices(){
        
        return new Promise((resolve, reject) => {
            
            let devices:Array<any> = [];
            let addedDevices:Array<string> = [];
            ble.scan([BluetoothUUID.SERVICE],5,(foundDevice) => 
                {

                    let newDevice = {address: foundDevice.id, name: foundDevice.name};
                    
                    if (newDevice.name && addedDevices.indexOf(newDevice.name) < 0){
                        //alert(JSON.stringify(foundDevice))
                        devices.push(newDevice);
                        addedDevices.push(newDevice.name);
                    }
                }
            ,(failure) => this.onFail(failure));   
            
            setTimeout(() => {
                resolve(devices);
            },5500);
            
            
        });        
        
        
       
    }
    
    private selectDevice(devices){
        this.events.publish('meter:listingselect');
        
        this.deviceCount = devices.length;
        
        
        let inputs:Array<any> = [];
        let inputsIndexed = {};
        for (var index in devices){
            let device = devices[index]
            inputs.push({
                type:"radio",
                name:"device" + index,
                value:index,
                label:device.name
            });
            inputsIndexed[index] = device;
        }
        

        
        this.alertCtrl.create({
            header: 'Select Device',
            inputs: inputs,
            buttons: [
            {
                text: 'Cancel',
                role: 'cancel',
                handler: data => {
                  console.log('Cancel clicked');
                }
            },
            {
                text: 'Connect',
                handler: deviceIndex => {
                  if (deviceIndex){
                      this.events.publish('meter:listingselected');
                      let device = inputsIndexed[deviceIndex];
                      this.storage.set("previouslyConnectedMeter", device.address);
                      this.connect(device);
                  }
                }
            }
          ]
        }).then((el) => {
            el.present();
        });       
    }
    
    
    private changeDevice(devices){
        
        let inputs:Array<any> = [];
        let inputsIndexed = {};
        for (var index in devices){
            let device = devices[index]
            inputs.push({
                type:"radio",
                name:"device" + index,
                value:index,
                label:device.name
            });
            inputsIndexed[index] = device;
        }
        
        this.alertCtrl.create({
            header: 'Select Device',
            subHeader: 'The devices in your bluetooth network have changed',
            message: 'If you wish to change to another meter, select it from the list, otherwise press cancel to continue on the current meter.',
            inputs: inputs,
            buttons: [
            {
                text: 'Cancel',
                role: 'cancel',
                handler: data => {
                  console.log('Cancel clicked');
                }
            },
            {
                text: 'Connect',
                handler: deviceIndex => {
                  if (deviceIndex){
                      
                      let device = inputsIndexed[deviceIndex];
                        this.events.publish('meter:connecting');
                        this.storage.set("previouslyConnectedMeter", device.address);
                        this.connect(device);
                        
                        
                  }
                }
            }
          ]
        }).then((el) => {
            el.present();
        });      
    }    

    public request(device) {
        //console.log('[BluetoothService] - request() :: Sending transaction request to the BT device');

        this.events.publish('meter:reading');     
        let chunkedBuffer = (new BluetoothTransactionRequest()).toChunkedArrayBuffer();
        
        for (let chunk of chunkedBuffer){
            ble.write(device.address, BluetoothUUID.SERVICE, BluetoothUUID.WRITE, chunk,
                () => this.onRequestComplete(),
                (failure) => this.onFail(failure));            
        }           
        


    }

    public requestAll() {
        //console.log('[BluetoothService] - requestAll() :: Sending transaction request to the BT device');
    }

    public acknowledge(device) {
        //console.log('[BluetoothService] - acknowledge() :: Sending acknowledge to the BT device');

        let chunkedBuffer = (new BluetoothAcknowledgement()).toChunkedArrayBuffer();    
        for (let chunk of chunkedBuffer){
            ble.write(device.address, BluetoothUUID.SERVICE, BluetoothUUID.WRITE, chunk,
                () => this.onAcknowledge(device),
                (failure) => this.onFail(failure));            
        }                 

    }

    protected onConnect(device:any, deviceDetails:any) {
        //console.info(`[BluetoothService] - onConnect() :: Connected to BT device ${device.address}.`);
        this.alertCtrl.create({
          header: 'Success',
          subHeader: 'You are now connected to ' + device.name,
          buttons: ['Dismiss']
        }).then((el) => {
            el.present();
        });  
        
        /*
         * for debugging purposes
        let alertBox2 = this.alertCtrl.create({
          title: 'Device Details',
          subTitle: JSON.stringify(deviceDetails),
          buttons: ['Dismiss']
        });
        alertBox2.present();     
        
        */       

        ble.startNotification(device.address, BluetoothUUID.SERVICE, BluetoothUUID.READ,
            (data) => this.onData(data, device), 
            (failure) => this.onFail(failure));
            
        this.request(device);
        
        /*
        setTimeout(() => {
            let alertBox = this.alertCtrl.create({
              title: 'Request Again',
              subTitle: 'Make the request again',
              buttons: [      {
                text: 'Cancel',
                role: 'cancel',
                handler: () => {
                  this.events.publish('meter:complete');
                  ble.disconnect(device.address); 
                }
              },
              {
                text: 'Request',
                handler: () => {
                    this.request(device);
                }
              }]
            });
            alertBox.present();              
        },10000);   
        
        */      
        
        
        
    }

    protected onData(data, device) {
        
        /*
        let alertBox = this.alertCtrl.create({
          title: 'Response Received',
          subTitle: String.fromCharCode.apply(null, new Uint8Array(data)),
          buttons: ['Dismiss']
        });
        alertBox.present();  
        
        */       
        
        
        let response;
        try {
            response = new BluetoothResponse(data,false);
        } catch (error) {
            
            this.chunkedResponse = this.chunkedResponse + String.fromCharCode.apply(null, new Uint8Array(data));
            
            /*
            let alertBox2 = this.alertCtrl.create({
              title: 'Current Position',
              subTitle: this.chunkedResponse,
              buttons: ['Dismiss']
            });
            alertBox2.present();  
            
            */          
            
            try{
                
                response = new BluetoothResponse(this.chunkedResponse,true);
         
            } catch(error){
            

                /*
                let alertBox3 = this.alertCtrl.create({
                  title: 'Error converting response',
                  subTitle: error,
                  buttons: ['Dismiss']
                });
                alertBox3.present();
                
                */      

            
            
                return;
                
            }
            
              /*  
            let alertBox = this.alertCtrl.create({
              title: 'Full Response Captured',
              subTitle: this.chunkedResponse,
              buttons: ['Dismiss']
            });
            alertBox.present();               
            */
            this.chunkedResponse = "";
        
            //this.onFail(error);

        }

        // If the response is just a boundary, drop the response.
        if(response.isBoundaryOnly()) {
            return;
        }

        // If the response is not empty, store it until we get an empty response
        if (!response.isEmpty()) {
            //console.log('[BluetoothService] - onData() :: Received data from the BT device', response);
            this.responses.push(response);
              
            
            this.acknowledge(device);
        }
        // If the response is empty, we reached the end of the stack, send the last data 
        else {
            //console.log('[BluetoothService] - onData() :: Finished receiving data from the BT device.');
            if(this.responses && this.responses.length > 0) {
                //console.log('[BluetoothService] - onData() :: Publishing the received data.', this.responses);
                //console.info('[BluetoothService] - onData() :: Publishing the received data.');
                this.events.publish('meter:data', this.responses);
            }

            this.events.publish('meter:complete');
            ble.disconnect(device.address); 
        }
    }

    protected onRequestComplete() {
        
        /*
        let alertBox = this.alertCtrl.create({
          title: 'Data Requested',
          subTitle: 'Data successfully requested from meter',
          buttons: ['Dismiss']
        });
        alertBox.present();     
        
        */
        
   
        
        //console.log('[BluetoothService] - onRequestComplete() :: Transaction request sent to BT device');
    }

    protected onAcknowledge(device) {
        //console.log('[BluetoothService] - onAcknowledge() :: Acknowledge sent to BT device');      
        
        this.request(device);
    }

    protected onFail(failure) {
        
        let message = "";
        
        try {
            message = JSON.stringify(failure);
        }
        catch(e){
            message = failure;
        }
        
        
        console.warn('[BluetoothService] - onFail() :: ', failure);
        this.events.publish('meter:complete');

        let toast = this.toastController.create({
            message: failure,
            duration: 5000,
            cssClass: 'toast-error'
        }).then((el) => {
            el.present();
        });
        this.events.publish('database:synced');
        
        this.storage.get("previouslyConnectedMeter").then((uuid) => {
            if (uuid){
                ble.disconnect(uuid);
            }
        });               
        
    }
}
